"use client";

import { ThemeProvider } from "next-themes";
import { AuthProvider } from "@/providers/AuthProvider";  // ‚úÖ ‡πÅ‡∏¢‡∏Å‡πÑ‡∏ü‡∏•‡πå Provider ‡∏à‡∏£‡∏¥‡∏á

export default function Providers({ children }) {
  return (
    <ThemeProvider
      attribute="class"
      defaultTheme="light"
      enableSystem={false}
      storageKey="theme"
      disableTransitionOnChange
    >
      <AuthProvider>{children}</AuthProvider>
    </ThemeProvider>
  );
}